import { SetCookieOptionParamter, SetCookieParamter } from './types';

export const setCookie = (setCookieParameter: SetCookieParamter) => {
  const { name, value, ...options } = setCookieParameter;
  const updatedCookie = `${encodeURIComponent(name)}=${encodeURIComponent(value)}`;

  const cookieOptionFn: {
    [K in Required<keyof SetCookieOptionParamter>]: (optionValue: string) => string;
  } = {
    path: (path: string) => `path=${path || '/'};`,
    domain: (domain: string) => `domain=${domain};` || '',
    expires: (expires: string) => {
      if (!expires) {
        return '';
      }

      const date = new Date();
      date.setDate(date.getDate() + +expires);

      return `expires=${date.toUTCString()};`;
    },
    secure: (secure: string) => (secure ? 'secure' : ''),
  };

  const cookieOptionEntry = Object.entries(options) as [
    keyof SetCookieOptionParamter,
    NonNullable<SetCookieOptionParamter[Required<keyof SetCookieOptionParamter>]>,
  ][];

  const cookieOptions = cookieOptionEntry.reduce((acc, [key, value]) => {
    return acc + `${cookieOptionFn[key](value)} `;
  }, '');

  document.cookie = `${updatedCookie}; ${cookieOptions}`;
};
