/**
 * @description 특정 name을 갖는 cookie의 value를 찾는 함수
 * @param name 찾고자 하는 cookie의 name
 * @returns 쿠키가 존재하면 쿠키의 value를 없다면 null
 */
export const getCookie = (name: string) => {
  const getCookieRegExp = new RegExp(`(^|;) ?${name}=([^;]*)(;|$)`);
  const matchedCookieInfo = document.cookie.match(getCookieRegExp);
  return matchedCookieInfo ? decodeURIComponent(matchedCookieInfo[2]) : null;
};
